<template>
  <div
    class="indexCss"
    style="background-image:url('https://cdn.magdeleine.co/wp-content/uploads/2021/04/48036262281_20ecd6de28_o-1400x932.jpg')"
  >
    <div class="nav">
      <h2
        v-for="item in list"
        :key="item"
        @click="goOther(item)"
      >{{item}}</h2>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      list: [
        "前端开发规范",
        "面试对答",
        "笔试试题",
        "JS知识总结",
        "Vue知识总结",
      ],
    };
  },
  computed: {},
  filter: {},
  activated: {},
  mounted() {},
  methods: {
    goOther(e) {
      switch (e) {
        case "前端开发规范":
          this.$router.push({
            path: "Rules",
          });
          break;
        default:
          break;
      }
    },
  },
};
</script>

<style  scoped lang="less">
.indexCss {
  width: 100%;
  height: 100vh;
  background-size: cover;
  .nav {
    padding: 80px;
    color: #fff;
    // font-family: "楷体", "楷体_GB2312";
    font-family: "Times New Roman", Times, serif;
    font-size: 100%;
    font-size: 20px;
    h2 {
      width: 200px;
      color: #fff;
      margin: 20px 0;
      padding: 10px 20px;
      border-radius: 20px;
      background-color: rgba(241, 229, 229, 0.1);
    }
  }
}
</style>
